.menu__right__item figcaption, .menu__right, .menu__wrapper, .menu, .footer__socials__link, .gallery__item__socials__link, .cover {
  justify-content: center;
  align-items: center;
  display: flex;
}

html, body, p, ol, ul, li, dl, dt, dd, blockquote, figure, fieldset, legend, textarea, pre, iframe, hr, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal;
}

ul {
  list-style: none;
}

button, input, select {
  margin: 0;
}

html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

img, video {
  height: auto;
  max-width: 100%;
}

iframe {
  border: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td, th {
  padding: 0;
}

*, :after, :before {
  box-sizing: border-box;
}

html {
  color: #272727;
  scroll-behavior: smooth;
  background: #fff;
  font-family: Roboto;
  font-size: .902778vw;
  line-height: 1;
}

@media (max-width: 767px) {
  html {
    font-size: 2.34375vw;
  }
}

body {
  max-width: 1920px;
  overscroll-behavior-x: none;
  caret-color: #0000;
  margin: auto;
}

main {
  min-height: 96vh;
}

footer {
  min-height: 4vh;
}

a {
  color: inherit;
  outline: none;
  text-decoration: none;
}

button {
  color: inherit;
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
}

h1, h2, h3 {
  font-family: Roboto Condensed;
  font-weight: 400;
}

p {
  font-size: 1.2rem;
}

img {
  pointer-events: none;
}

.cover {
  display: none;
}

.gallery {
  flex-direction: row;
  place-content: flex-start center;
  gap: 4.5rem;
  margin: 24vh auto auto;
  padding: 0 7vw;
  display: flex;
}

.gallery__one {
  margin-top: 14vh;
}

.gallery__three {
  margin-top: 20vh;
}

.gallery__one, .gallery__two, .gallery__three {
  width: 33%;
  flex-direction: column;
  gap: 7rem;
  display: flex;
}

@media (max-width: 767px) {
  .gallery__one, .gallery__two, .gallery__three {
    width: 100%;
    margin-top: 0;
  }

  .gallery {
    flex-direction: column;
    place-content: flex-start center;
    padding: 0 2.2vw;
    display: flex;
  }
}

.gallery__item {
  perspective: 1024px;
}

.gallery__item__bottom {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1.2rem;
  display: flex;
}

.gallery__title {
  color: #272727;
  font-size: 1.2rem;
  position: relative;
  overflow: visible;
}

.gallery__title:before {
  content: "";
  width: 125%;
  height: 207%;
  z-index: 1;
  opacity: 0;
  background-color: #cccccc1a;
  border-radius: 10px;
  transition: opacity .3s;
  position: absolute;
  top: -50%;
  left: -11%;
}

.gallery__title:hover:before {
  opacity: 1;
}

.gallery__title:active:before {
  opacity: 1;
  background-color: #e7bdff33;
}

.gallery__item__socials {
  align-items: center;
  gap: 1rem;
  display: flex;
}

@media (max-width: 767px) {
  .gallery__item__socials {
    gap: 2.2rem;
  }
}

.gallery__item__socials__link {
  height: 100%;
  width: calc(.7rem + 1vh);
  opacity: .55;
  transition: opacity .2s;
}

@media (max-width: 767px) {
  .gallery__item__socials__link {
    width: calc(1rem + 1vh);
  }
}

.gallery__item__socials__link:hover {
  opacity: .85;
}

.gallery__item__socials__link:active {
  opacity: 1;
}

.gallery__item__socials__icon {
  width: inherit;
  height: inherit;
}

.menu--open > .nav {
  box-shadow: none;
  background: none;
}

.nav {
  width: 100%;
  z-index: 3;
  padding: 2vh 2.2vw;
  transition: all 1s ease-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

@media (max-width: 767px) {
  .nav {
    background: #fff;
    border-radius: 0 0 4px 4px;
    padding: 2vh 6.2vw;
    box-shadow: 0 6px 24px #0000000d, 0 0 0 1px #00000014;
  }
}

.nav__left {
  flex-basis: 40%;
}

.nav__right {
  width: 100%;
  justify-content: flex-end;
  align-items: center;
  gap: 7.5rem;
  font-size: 1.25rem;
  display: flex;
}

.nav__logo {
  height: auto;
  width: max-content;
  display: block;
  position: relative;
}

.nav__logo:hover > .nav__logo__image--hover, .nav__logo:active > .nav__logo__image--hover {
  opacity: 1;
}

.nav__logo__image {
  max-width: 24px;
  height: auto;
  z-index: 1;
}

.nav__logo__image--hover {
  z-index: 2;
  max-width: 24px;
  height: auto;
  opacity: 0;
  transition: opacity .4s ease-out;
  position: absolute;
  top: 0;
  left: 0;
}

.nav__menu__link {
  color: #4d4d4d;
}

.nav__menu__link:hover {
  color: #000;
}

.nav__menu__icon {
  width: 32px;
  height: 32px;
  cursor: pointer;
  position: relative;
}

.nav__menu__icon__line {
  width: 100%;
  border: 1px solid #272727;
  border-radius: 2px;
  transition: all .44s ease-out;
  position: absolute;
  top: 0;
  left: 0;
}

.nav__menu__icon__line:first-of-type {
  transform: translateY(0)rotate(0);
}

.nav__menu__icon__line:nth-child(2) {
  opacity: 1;
  transform: translateY(8px);
}

.nav__menu__icon__line:nth-child(3) {
  transform: translateY(16px)rotate(0);
}

.nav__menu__icon--active > .nav__menu__icon__line:first-of-type {
  transform: translateY(8px)rotate(45deg);
}

.nav__menu__icon--active > .nav__menu__icon__line:nth-child(2) {
  opacity: 0;
  transition: all .1s ease-out;
  transform: translateY(8px);
}

.nav__menu__icon--active > .nav__menu__icon__line:nth-child(3) {
  transform: translateY(8px)rotate(-45deg);
}

.footer {
  z-index: 2;
  justify-content: flex-start;
  align-items: center;
  margin-top: 13vh;
  margin-left: 3.5%;
  padding: 5vh 0;
  display: flex;
  position: relative;
}

.footer__logo {
  opacity: .78;
  margin-right: 4.19rem;
}

.footer__logo__image {
  max-height: 86px;
}

.footer__logo:hover {
  opacity: 1;
}

.footer__block {
  min-width: 14vw;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  padding: 0 2rem;
  display: flex;
}

@media (max-width: 767px) {
  .footer__block {
    min-width: 27vw;
  }
}

.footer__text {
  text-align: right;
  color: #5e5e5e;
  cursor: default;
}

.footer__text h1 {
  font-size: 1.4rem;
  font-style: italic;
}

.footer__text p {
  font-size: .9rem;
}

.footer__socials {
  width: 100%;
  justify-content: space-around;
  display: flex;
}

@media (max-width: 767px) {
  .footer__socials {
    gap: 1rem;
  }
}

.footer__socials__link {
  height: 1.45rem;
  opacity: .85;
}

@media (max-width: 767px) {
  .footer__socials__link {
    height: 2rem;
  }
}

.footer__socials__link:hover {
  opacity: 1;
}

.footer__socials__icon {
  height: inherit;
  padding-left: 8px;
}

.menu {
  height: 100vh;
  width: 100%;
  z-index: 2;
  visibility: hidden;
  opacity: 0;
  background: #fff;
  padding: 0 7%;
  position: fixed;
  top: 0;
  left: 0;
}

@media (max-width: 767px) {
  .menu__wrapper {
    height: 100vh;
    flex-direction: column-reverse;
    justify-content: flex-start;
    padding-bottom: 12%;
  }
}

.menu__left {
  width: 70%;
  height: max-content;
  box-shadow: 0 0 29px #64646f1f;
}

@media (max-width: 767px) {
  .menu__left {
    width: 100%;
  }
}

.menu__item {
  max-height: 4vh;
  visibility: hidden;
  background: none;
  display: flex;
}

.menu__item__image--link {
  width: 60%;
  height: inherit;
  position: relative;
  overflow: hidden;
}

.menu__item__image--link img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.menu__item__image--link:before {
  content: "";
  width: 100%;
  height: 100%;
  opacity: 0;
  background: #00000026;
  transition: opacity .35s ease-out;
  position: absolute;
  top: 0;
  left: 0;
}

.menu__item__link {
  width: 40%;
  justify-content: space-between;
  align-items: center;
  padding: 0 1.8rem;
  transition: all .35s ease-out;
  display: flex;
}

.menu__item__link > .gallery__item__socials {
  gap: .5rem;
}

@media (max-width: 767px) {
  .menu__item__link > .gallery__item__socials {
    display: none;
  }
}

.menu__right {
  width: 40%;
  padding: 4%;
}

@media (max-width: 767px) {
  .menu__right {
    width: 60%;
    height: 40%;
  }
}

.menu__right__item figcaption {
  cursor: default;
  padding: 1rem;
  font-size: 1.2rem;
  font-weight: bold;
}

.menu__item__link--text {
  opacity: .8;
}

.menu__item:hover > .menu__item__link {
  background: #cccccc1a;
}

.menu__item:hover > .menu__item__image--link:before, .menu__item__link--text:hover {
  opacity: 1;
}

.menu__item__link--text:active {
  opacity: 1;
  font-weight: bold;
}

.menu__item__image--link:active:before {
  filter: brightness(1.2);
  background: #e7bdff33;
}

